/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

.wp-block-pixfort-core-pix-small-search {
	
	.shadow-sm {
		box-shadow: 0 1px 5px 0 rgba(0, 0, 0, 0.15) !important;
	}
	.d-flex {
		display: flex;
	}

	.btn:not(:disabled):not(.disabled) {
		cursor: pointer;
	}
	body:not(.rtl) .btn-search {
		border-top-left-radius: 0;
		border-bottom-left-radius: 0;
	}
	[type=button]:not(:disabled), [type=reset]:not(:disabled), [type=submit]:not(:disabled), button:not(:disabled) {
		cursor: pointer;
	}
	button, [type="button"], [type="reset"], [type="submit"] {
		-webkit-appearance: button;
	}
	.navbar-light.navbar-nav.nav-link.text-body-default, .text-body-default {
		color: #6c757d !important;
	}
	.btn-white {
		--pix-btn-color: #333;
		background-color: #fff;
	}
	.btn-gray-1, .btn-gray-2, .btn-gray-3, .btn-gray-4, .btn-gray-5, .btn-gray-6, .btn-gray-7, .btn-gray-8, .btn-gray-9, .btn-primary, .btn-secondary, .btn-green, .btn-cyan, .btn-yellow, .btn-orange, .btn-red, .btn-blue, .btn-brown, .btn-purple, .btn-white, .btn-black, .btn-primary-light, .btn-secondary-light {
		color: var(--pix-btn-color);
		box-shadow: none;
		text-shadow: 0px;
	}
	.btn {
		font-weight: 400;
		color: #6c757d;
		text-align: center;
		vertical-align: middle;
		-webkit-user-select: none;
		-moz-user-select: none;
		user-select: none;
		background-color: transparent;
		border: 0rem solid transparent;
		margin-right: 10px;
		padding: 0.5625rem 1.125rem;
		font-size: 0.875rem;
		line-height: 1.8;
		border-radius: 4px;
		transition: all 0.15s ease-in-out;
		line-break: normal;
		display: flex;
		align-items: center;
	}
	.m-0 {
		margin: 0 !important;
	}


.pix-small-search {
    position: relative !important;
    // z-index: 999999999;
}

.shadow-sm, .shadow, .shadow-lg, .shadow-hover-sm, .shadow-hover, .shadow-hover-lg, .shadow-inverse-sm, .shadow-inverse, .shadow-inverse-lg {
    backface-visibility: hidden;
    transition: all 0.4s cubic-bezier(0.165, 0.84, 0.44, 1);
}
.bg-white {
    background-color: #fff !important;
}
.bg-white {
    background-color: #fff !important;
}
.position-relative {
    position: relative !important;
}
.rounded-lg {
    border-radius: .3rem !important;
}
	.pix-small-search .pix-search-btn, .pix-small-search .form-control, .pix-small-search .post-password-form input[type="password"], .post-password-form .pix-small-search input[type="password"] {
		min-height: 60px !important;
	}
	.pix-small-search .form-control, .pix-small-search .post-password-form input[type="password"], .post-password-form .pix-small-search input[type="password"] {
		font-size: 14px;
	}
	.navbar-light.navbar-nav.nav-link.text-body-default, .text-body-default {
		color: #6c757d !important;
	}
	b, strong, .font-weight-bold {
		font-weight: 600 !important;
	}
	.form-control, .post-password-form input[type="password"] {
		background-color: white;
		border: 0px solid transparent;
		box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
	}
	.form-control-lg {
		height: calc(3.1875rem + 0rem);
		font-size: 1.125rem;
		line-height: 1.5;
		border-radius: 0.3rem;
	}
	.form-control, .post-password-form input[type="password"] {
		display: block;
		width: 100%;
		height: calc(2.7rem + 0rem);
		padding: 0.5625rem 1.125rem;
		font-size: 0.875rem;
		font-weight: 400;
		line-height: 1.8;
		color: #495057;
		background-color: #fff;
		background-clip: padding-box;
		border: 0rem solid #ced4da;
		border-radius: 0.25rem;
		transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
	}
	.shadow-0 {
		box-shadow: none !important;
	}
}